.BannerPadding {
  padding:0 1.8rem 5rem 1.8rem;
  background-color: #ffe0b2;

.BannerArea {
  height: 40vh;
  background: linear-gradient(rgba(240, 240, 240, 0.250), rgba(255, 255, 255, 0.150)), url("../../../assets/images/banner/Banner_Restaurant.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  color: whitesmoke;
  border-radius: 12px;

  .Container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100%;
      font-size: 2rem;
  }

  .Titulo {
      text-transform: uppercase;
      margin:0;
      text-shadow: 1px 1px 4px #000000;
  }
}
}